var get_wallet_manager = function (qthis) {

    return new Promise(function (resolve) {
        if (qthis.$store.state.wallet_manager == undefined) {
            // reject()
            resolve(new WalletManager())
            // console.log("no wallet")
        } else {
            qthis.$store.state.wallet_manager.get_current_wallet_network().then(() => {
                resolve(qthis.$store.state.wallet_manager)

            })

        }
    })
}

var get_nft_manager = function (qthis) {
    return new Promise(function (resolve) {
        resolve(qthis.$store.state.nft_manager)
    })
}

var get_account = function (qthis) {
    return get_wallet_manager(qthis).then((x) => x.get_account_details())
}

var calc_price = function (t) {
    var res = 0.0
    if (t == '') {
        res = this.nft.price
    }
    else if (t == undefined) {
        res = this.nft.price
    } else {
        res = t
    }
    res = res + ""
    if (res.indexOf(".") > -1) {
        return res
    }
    return parseFloat(res + "").toFixed(1)
}

class MineItem {
    appli;
    nft;
    run_state;
    worker;
    strongest_nonce_found;
    strongest_nonce_found_strength;
    decoy_nonce;

    constructor(aapl, nft) {
        this.appli = aapl
        this.nft = nft

        this.run_state = ref(ENUMS.WORKER.STOPPED)
        this.strongest_nonce_found = ref(undefined)
        this.strongest_nonce_found_strength = ref(nft.get_strength())
        if (this.appli.$store.state.found_nonces[this.nft.get_id()] != undefined) {
            vsetter(this, "strongest_nonce_found", (this.appli.$store.state.found_nonces[this.nft.get_id()]))
            vsetter(this, "strongest_nonce_found_strength", (get_nft_strength_with_nonce(this.nft.get_curhash(), vget(this, "strongest_nonce_found"))))
        }
        this.decoy_nonce = ref("")


        this.initialize()
        this.set_hash()
        this.continue()

    }
    initialize() {
        var qthis = this
        vsetter(this, "run_state", ENUMS.WORKER.RUNNING)

        if (this.worker == undefined) {
            this.worker = new Worker('/miner.js')

            this.worker.onmessage = function (evv) {
                var ev = evv.data
                if (ev.res == "found") {
                    vsetter(qthis, "run_state", ENUMS.WORKER.STOPPED)

                    vsetter(qthis, "strongest_nonce_found", ev.data.nonce)

                    vsetter(qthis, "strongest_nonce_found_strength", qthis.nft.get_strength_with_nonce(ev.data.nonce))
                    save_found_nonces(qthis)

                } else if (ev.res == "notfoundyet") {

                    vsetter(qthis, "decoy_nonce", ev.data)
                    qthis.worker.postMessage(JSON.stringify({ type: "continue" }))

                }
            }

        }
    }
    close_mine() {
        if (this.worker != undefined)
            this.worker.terminate()
        this.worker = undefined
        vsetter(this, "run_state", ENUMS.WORKER.STOPPED)
    }
    continue() {
        if (this.worker == undefined) {
            this.initialize()
        }

        this.worker.postMessage(JSON.stringify({ type: "continue" }))
        vsetter(this, "run_state", ENUMS.WORKER.RUNNING)

    }
    set_hash() {
        this.worker.postMessage(JSON.stringify({
            type: "set_hash", data: {
                curhash: this.nft.get_curhash()
                , strongest_nonce: vget(this, "strongest_nonce_found")
                , strongest_nonce_found_strength: vget(this, "strongest_nonce_found_strength")
            }
        }))
    }

}
class NFRItem {
    nft;
    appli;
    type;
    strongest_nonce_found;
    strongest_nonce_found_strength;
    constructor(appli, nft, strongest_nonce_found) {
        this.appli = appli
        this.nft = nft
        this.strongest_nonce_found = strongest_nonce_found
        this.type = ENUMS.HEIST_TYPE.MINED
        if (strongest_nonce_found == undefined) {
            this.type = ENUMS.HEIST_TYPE.MANUAL

        }
    }
    lock_nfr() {
        var qthis = this

        return new Promise(function (resolve) {


            qthis.query_nfr().then(x => {
                if (x.result.status == "success") {
                    resolve({ result: "goodlock" })

                } else {
                    return get_account(qthis.appli).then((acc) => {



                        return get_wallet_manager(qthis.appli).then((x) => {
                            var signingCmd = {
                                "pactCode": "(free.coal.no-front-running-me \"" + calc_no_front_run(qthis) + "\")",
                                "caps": [

                                ],
                                "sender": acc.name,
                                "ttl": 600,
                                "gasLimit": qthis.appli.$store.state.settings.GAS_LIMIT,
                                "chainId": vget(x, "chain_id"),
                                "gasPrice": 0.000001,
                                "envData": {
                                    "nfr-ks": [acc.public_key],
                                },
                                "signingPubKey": acc.public_key,
                                "networkId": vget(x, "network_id")
                            }
                            return x.sign(signingCmd).then(y => x.send(y))
                        }).then(x => {
                            x = Object.values(x)[0]
                            return new Promise(function (resolve, reject) {


                                if (x.result.status == "failure") {
                                    reject({ result: "failure" })

                                } else {
                                    resolve({ result: "success" })
                                }
                            })
                        })

                    })
                }
            })

        })
    }
    query_nfr() {
        var qthis = this
        return get_account(qthis.appli).then(acc => {


            return get_wallet_manager(qthis.appli).then(x => {
                var signingCmd = {
                    "pactCode": "(free.coal.verify-nfr \"" + calc_no_front_run(qthis) + "\")",
                    "caps": [

                    ],
                    "sender": acc.name,
                    "ttl": 600,
                    "gasLimit": qthis.appli.$store.state.settings.GAS_LIMIT,
                    "chainId": vget(x, "chain_id"),
                    "gasPrice": 0.000001,
                    "envData": {
                        "nfr-ks": [acc.public_key],
                    },
                    "signingPubKey": acc.public_key,
                    "networkId": vget(x, "network_id")
                }
                return x.sign(signingCmd).then(y => x.localfetch(y))
            })
        })


    }
    verify_nfr() {
        var qthis = this

        return new Promise(function (resolve, reject) {

            get_account(qthis.appli).then(acc => {
                qthis.query_nfr().then(x => {
                    if (x.result.status == "success") {
                        resolve({ result: ENUMS.NFR_STATE.LOCKED_IN_MY_NAME })

                    } else {


                        return get_wallet_manager(qthis.appli).then(x => {
                            var signingCmd = {
                                "pactCode": "(free.coal.verify-nfr \"" + calc_no_front_run(qthis) + "\")",
                                "caps": [

                                ],
                                "sender": acc.name,
                                "ttl": 600,
                                "gasLimit": qthis.appli.$store.state.settings.GAS_LIMIT,
                                "chainId": vget(x, "chain_id"),
                                "gasPrice": 0.000001,
                                "envData": {
                                    "nfr-ks": [acc.public_key],
                                },
                                "signingPubKey": acc.public_key,
                                "networkId": vget(x, "network_id")
                            }

                            return x.sign(signingCmd).then(y => x.send(y))
                        }).then(x => {
                            x = Object.values(x)[0]
                            if (x.result.status == "failure") {
                                if (x.result.error.message.toLowerCase().indexOf("row not found") > -1) {
                                    reject({ result: ENUMS.NFR_STATE.NOT_FOUND })
                                } else if (x.result.error.message.toLowerCase().indexOf("keyset failure") > -1) {
                                    reject({ result: ENUMS.NFR_STATE.LOCKED_IN_OTHER_NAME })

                                }
                                reject({ result: ENUMS.NFR_STATE.NOT_FOUND })

                            } else {
                                resolve({ result: ENUMS.NFR_STATE.LOCKED_IN_MY_NAME })
                            }
                        })

                    }
                })


            })
        })

    }
    submit_nonce() {
        var qthis = this

        return new Promise(function (resolve, reject) {

            var meth = "strengthen-nft"
            get_account(qthis.appli).then(acc => {

                if (qthis.nft.get_full_owner() != acc.name) {
                    meth = "steal-nft"
                }


                get_wallet_manager(qthis.appli).then(x => {
                    var signingCmd = {
                        "pactCode": "(free.coal." + meth + " \"" + qthis.nft.get_id() + "\" \"" + qthis.strongest_nonce_found + "\")",
                        "caps": [],
                        "sender": acc.name,
                        "ttl": 600,
                        "gasLimit": qthis.appli.$store.state.settings.GAS_LIMIT,
                        "chainId": vget(x, "chain_id"),
                        "gasPrice": 0.000001,
                        "envData": {
                            "nfr-keyset": [acc.public_key],
                        },
                        "signingPubKey": acc.public_key,
                        "networkId": vget(x, "network_id")
                    }

                    return x.sign(signingCmd).then(y => x.send(y))
                }).then(x => {
                    x = Object.values(x)[0]
                    if (x.result.status == "success") {
                        resolve(x, meth)

                    } else {
                        reject(x)
                    }
                })

            })
        })
    }

}
