<template>
    <div class="mbody">
        <h1 style="justify-content: center;display: flex;">List NFTs</h1>

        <hr class="rounded">

        <div class="genholder genholderh">

            <button @click="seeker -= 20; $store.state.view_list_last_seeker = this.seeker">Back</button>
            <div class="centex"><input class="mytex mytexredbord" v-model="seeker_text"
                    @keyup.enter="edit_mode_enable = false; seeker_data = parseInt(seeker_text); reload()"
                    v-show="edit_mode_enable == true" type="text" autocomplete="off" id="seeker_id_focus" /><button
                    v-show="edit_mode_enable == false"
                    @click="edit_mode_enable = true; seeker_text = seeker_data + ''; mfocus()">{{ seeker }} - {{ seeker
                            +
                            20
                    }}</button></div>
            <button @click="seeker += 20; $store.state.view_list_last_seeker = this.seeker">Forward</button>
        </div>
        <LoadingComp v-show="is_loading"></LoadingComp>
        <div class="genholder genholderv">


            <table class="wbord">
                <tr class="wbord">
                    <th class="wbord">ID</th>
                    <th class="wbord">Attribs</th>

                    <th class="wbord">Strength</th>
                    <th class="wbord">Price $KDA</th>
                    <th class="wbord">on sale?</th>
                    <!-- <th class="wbord">sale until</th> -->
                    <th class="wbord">Owner</th>
                    <th class="wbord">Minter</th>
                    <th class="wbord">Mint Date</th>
                </tr>
                <tr class="wbord" v-bind:class="nft.is_owned_by_wallet() ? 'ownerclass' : 'notownerclass'"
                    v-show="!is_loading" v-for="(nft, nftidx) in loaded_nfts" :key="nftidx">
                    <td> {{ nft.get_id() }}</td>
                    <td>
                        <CoalRend style="transform: scale(0.7);" :nft="nft"></CoalRend>
                    </td>
                    <td> <button v-show="!nft.is_owned_by_wallet()" @click="send_to_heist(nft)">
                            <span class="stealbutt" style="margin-right: 2%;">steal</span>
                            <span style="color: yellow;">
                                {{ nft.get_strength() }}</span> / 256
                        </button>
                        <div v-show="nft.is_owned_by_wallet()">

                            {{ nft.get_strength() }} / 256
                        </div>
                    </td>
                    <td>
                        <button @click="buy_nft(nft)" v-show="nft.get_sale_state() && (!nft.is_owned_by_wallet())">
                            <span style="color:yellow">
                                {{ nft.get_price()
                                }}
                            </span>
                            <span v-show="nft.get_sale_state()" class="buybutt" style="margin-left: 2%;">buy</span>
                        </button>
                        <div v-show="!nft.get_sale_state() || (nft.is_owned_by_wallet())">
                            {{ nft.get_price() }}
                        </div>
                    </td>
                    <td v-bind:class="nft.get_sale_state() ? 'yesclass' : 'noclass'"> {{ nft.get_sale_state() ? 'yes' :
                            'no'
                    }}
                    </td>
                    <!-- <td> {{ nft["sale-timeout"] < 0 ? 'always' : nft["sale-timeout"] }}</td> -->
                    <td>
                        <div class="ownertd">{{ nft.get_owner() }}</div>
                    </td>
                    <td> {{ nft.get_minter() }}</td>
                    <td> {{ nft.get_date_formatted() }}</td>
                </tr>
            </table>
        </div>

    </div>
</template>

<script>
import LoadingComp from '../LoadingComp.vue';
import CoalRend from './CoalRend.vue';
import ENUMS from '../Enums';
import { get_nft_strength_from_hash, date_formatter, get_nft_manager } from '@/utility'


export default {
    name: ENUMS.VIEWS.LISTNFTS
    , data() {
        return {
            ENUMS: ENUMS
            , loading: true
            , loaded_nfts: []
            , seeker_data: this.$store.state.view_list_last_seeker
            , edit_mode_enable: false
            , seeker_text: 0
        };
    }
    , computed: {
        is_loading() {
            return this.loading
        }
        , seeker: {
            set: function (x) {
                var entervalue = this.seeker_data
                this.seeker_data = Math.min(x, 6950)
                this.seeker_data = Math.max(x, 0)
                if (entervalue != this.seeker_data) {
                    this.loading = true
                    this.reload()

                }

            }, get: function () {
                return this.seeker_data
            }
        }
    }
    , mounted() {
        this.reload()

    }
    , methods: {
        reload() {
            var qthis = this
            qthis.loading = true

            get_nft_manager(qthis).then((x) => x.list_nfts_from(qthis.seeker)).then((x) => {
                qthis.loading = false
                qthis.loaded_nfts = x
            }).finally(() => {
                qthis.loading = false

            })

        }, mfocus() {
            setTimeout(() => {

                document.getElementById('seeker_id_focus').focus()
            }, 100);
        }
        , buy_nft(nft) {
            nft.buy_nft()
        }
        , send_to_heist(nft) {
            this.$store.state.nfr_manager.add_nfr_from_nft(nft)
            this.$store.state.nfr_cur = nft.get_id()
            this.$store.state.current_view = ENUMS.VIEWS.HEISTS
        }
        , get_nft_strength_from_hash: get_nft_strength_from_hash
        , date_formatter: date_formatter
        , get_nft_manager: get_nft_manager
    }
    , components: { LoadingComp, CoalRend }
}

</script>

<style scoped>
tr:hover {
    background-color: rgb(99, 0, 0);
}

tr:hover:nth-child(1) {
    background-color: black
}

td>button:hover {
    background-color: rgb(124, 0, 0);

}

.buybutt {
    color: greenyellow;
}

.stealbutt {
    color: red;
}

.ownertd {
    color: yellow;
}

.ownerclass {
    background-color: rgb(34, 34, 34);
}

.yesclass {
    color: greenyellow;
}
</style>