<template>
    <div class="mbody tbody">
        <h1 style="justify-content: center;display: flex;" class="tit">MINT</h1>

        <hr class="rounded">

        <CoalDialogVue></CoalDialogVue>

        <div v-bind:class="baphclass" class="mint">
            <div class="baph">

                <img style="width: 100%" src="../../assets/a2.png" />
                <div class="texholder">

                    <div>Mint</div>
                    <div>Price : 13.69 $KDA + Soul</div>
                    <br>
                    <button class="msig" @click="sign_deal()" v-bind:disabled="is_loading">
                        <div v-show="!is_loading">SIGN</div>
                        <LoadingCompVue v-show="is_loading"></LoadingCompVue>
                    </button>

                </div>
            </div>
            <div class="">

            </div>

        </div>
    </div>
</template>

<script>

import ENUMS from '../Enums';
import LoadingCompVue from '../LoadingComp.vue';
import { get_account, get_nft_manager, mypolling } from '@/utility'
import CoalDialogVue from './CoalDialog.vue';


export default {
    name: ENUMS.VIEWS.MAIN
    , data() {
        return {
            ENUMS: ENUMS
            , is_loading: false
            , dialog_message: undefined
            , baphclass: ""
        }
    }, components: { LoadingCompVue, CoalDialogVue }, methods: {
        sign_deal: function () {
            var mintsnd = new Audio('/mint.mp3');
            var eviafsnd = new Audio('/evilaf.mp3');


            var qthis = this
            qthis.is_loading = true


            get_nft_manager(qthis).then((x) => x.mint_coal()).catch((e) => {
                qthis.dialog_message = "User rejected transaction"
                throw e

            }).then(x => {
                console.log(x)
                x = Object.values(x)[0]
                if (x.result.status == "success") {
                    mintsnd.play();
                    setTimeout(() => {
                        eviafsnd.play()
                        qthis.baphclass = "bascale"

                    }, 500);
                    setTimeout(() => {
                        qthis.$store.state.current_view = ENUMS.VIEWS.MYNFTS
                    }, 1500);
                } else {
                    qthis.dialog_message = "Something Wrong please check balance"

                }
            }).finally(() => {
                qthis.is_loading = false
            })

        }
        , get_account
        , get_nft_manager
        , mypolling
    }
}

</script>

